@import "variables";
@import "../../theming";

.u-form-field {
  margin-bottom: $u-form-field-margin;

  &,
  & * {
    box-sizing: border-box;
  }
}

.u-text-field-container {
  display: flex;
  flex-direction: column;
  font-family: $u-font-family-base;

  &,
  & * {
    box-sizing: border-box;
  }
}

.u-text-field-helper-text {
  position: relative;
  margin-top: $u-text-field-helper-text-margin;
  overflow: hidden;
  font-size: $u-text-field-helper-text-font-size;
  line-height: $u-text-field-helper-text-line-height;
}

.u-text-field-helper-text-invalid {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;

  &:only-child {
    position: static;
    display: none;
  }
}

.u-text-field-helper-text-default,
.u-text-field-helper-text-invalid {
  display: inline-block;
}

.u-text-field-helper-text-default:not(:only-child),
.u-text-field-helper-text-invalid {
  opacity: 0;
  transition: transform 1ms 200ms, opacity 150ms;
  transform: translate3d(0, -100%, 0);
}

.u-text-field:not(.invalid) ~ .u-text-field-helper-text .u-text-field-helper-text-default,
.u-text-field.invalid ~ .u-text-field-helper-text .u-text-field-helper-text-invalid {
  opacity: 1;
  transition: transform 200ms, opacity 150ms;
  transform: translate3d(0, 0, 0);

  &:only-child {
    display: inline-block;
  }
}

.u-text-field-leading-icon,
.u-text-field-trailing-icon {
  position: absolute;
  bottom: $u-text-field-icon-bottom;
  width: $u-text-field-icon-size;
  height: $u-text-field-icon-size;
  margin-bottom: -.5em;
  font-size: $u-text-field-icon-size;
  line-height: 1em;
  text-align: center;

  @if (mixin_exists(u-buttons-theme)) {
    .u-btn-borderless {
      margin-top: -($u-btn-borderless-size - $u-text-field-icon-size) / 2;
      margin-left: -($u-btn-borderless-size - $u-text-field-icon-size) / 2;
    }
  }
}

.u-text-field-leading-icon {
  left: 0;

  ~ .u-text-input {
    padding-left: $u-text-input-padding + $u-text-field-icon-size;
  }
}

.u-text-field-trailing-icon {
  right: 0;

  ~ .u-text-input {
    padding-right: $u-text-input-padding + $u-text-field-icon-size;
  }
}

.u-text-input input,
.u-text-input textarea,
.u-text-input button {
  flex: 1;
}

.u-text-input input,
.u-text-input textarea,
input.u-text-input,
textarea.u-text-input,
button.u-text-input {
  padding: 0;
  background: transparent;
  border: none;

  &:focus {
    outline: 0;
  }
}

.u-text-input,
input.u-text-input,
textarea.u-text-input,
button.u-text-input {
  display: flex;
  align-items: center;
  padding-top: $u-text-input-padding;
  padding-bottom: $u-text-input-padding - $u-text-field-border-width;
  padding-left: 0;
  font-size: $u-text-input-font-size;
  line-height: $u-text-input-line-height;
  border-bottom: $u-text-input-border;
  border-radius: inherit;

  &:not(textarea) {
    height: $u-text-input-height + $u-text-input-padding * 2;
  }
}

.u-text-field {
  position: relative;
  display: flex;
  flex-direction: column-reverse;

  &,
  & * {
    box-sizing: border-box;
  }

  label {
    align-self: flex-start;
    height: $u-text-field-label-height;
    margin-bottom: $u-text-field-label-margin;
    font-size: $u-text-field-label-font-size;
    line-height: $u-text-field-label-line-height;
  }

  &::before {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    height: $u-text-field-focus-border-width;
    pointer-events: none;
    content: "";
    border-bottom: $u-text-field-focus-border-width solid currentColor;
    transition: transform 350ms $awesome-cubic-bezier;
    transform: scale3d(0, 1, 1);
    will-change: transform;
  }

  &.focus,
  &.invalid {

    label {
      color: currentColor;
    }

    &::before {
      transform: scale3d(1, 1, 1);
    }
  }

  &.disabled .u-text-input,
  input.u-text-input[disabled] {
    border-bottom-style: dashed !important;
  }
}

.u-text-field.u-text-field-floating-label,
.u-text-field-box,
.u-text-field-outline {
  label {
    transition: transform 200ms, font-size 200ms;
  }

  .u-text-input::placeholder,
  .u-text-input-placeholder {
    transition: color 150ms;
  }

  .u-text-input * {
    transition: opacity 150ms;
  }

  &.empty:not(.focus):not(.u-text-field-no-label) {
    label {
      font-size: $u-text-input-font-size;
      pointer-events: none;
      transform: translate3d(0, ($u-text-field-label-height / 2) + $u-text-field-label-margin + ($u-text-input-height / 2) + $u-text-input-padding, 0);
    }

    .u-text-input::placeholder,
    .u-text-input-placeholder {
      color: transparent;
      transition: color 150ms 100ms;
    }

    .u-text-input * {
      opacity: 0;
      transition: opacity 150ms 100ms;
    }
  }
}

.u-text-field-box {
  border-radius: $u-text-field-box-border-radius;

  label {
    height: $u-text-field-box-label-height;
    padding-left: $u-text-field-box-padding-horizontal;
    margin-bottom: 0;
    line-height: $u-text-field-box-label-line-height;
  }

  .u-text-input {
    padding: $u-text-field-box-padding-top $u-text-field-box-padding-horizontal calc(#{$u-text-field-box-padding-bottom} - #{$u-text-field-border-width});
    margin-top: -$u-text-field-box-label-height;

    &:not(textarea) {
      height: $u-text-field-box-height;
    }
  }

  &.u-text-field-no-label {
    .u-text-input {
      padding-top: $u-text-field-box-no-label-padding-vertical;
      padding-bottom: $u-text-field-box-no-label-padding-vertical;
      margin-top: 0;
    }
  }

  &.empty:not(.focus) {
    label {
      transform: translate3d(0, ($u-text-field-box-height / 2) - ($u-text-field-box-label-height / 2), 0) !important;
    }
  }

  ~ .u-text-field-helper-text {
    margin-right: $u-text-field-box-padding-horizontal;
    margin-left: $u-text-field-box-padding-horizontal;
  }

  .u-text-field-leading-icon,
  .u-text-field-trailing-icon {
    bottom: 50%;
  }

  .u-text-field-leading-icon {
    left: $u-text-field-icon-margin;

    ~ .u-text-input,
    ~ label {
      padding-left: calc(#{$u-text-field-icon-margin + $u-text-field-box-padding-horizontal} + #{$u-text-field-icon-size});
    }
  }

  .u-text-field-trailing-icon {
    right: $u-text-field-icon-margin;

    ~ .u-text-input,
    ~ label {
      padding-right: calc(#{$u-text-field-icon-margin + $u-text-field-box-padding-horizontal} + #{$u-text-field-icon-size});
    }
  }

  &.disabled .u-text-input,
  input.u-text-input[disabled] {
    border-bottom-style: solid !important;
  }
}

.u-text-field-outline {
  label {
    height: $u-text-field-outline-label-height;
    padding-right: $u-text-field-outline-label-padding;
    padding-left: $u-text-field-outline-label-padding;
    margin-bottom: 0;
    margin-left: $u-text-field-outline-padding - $u-text-field-outline-label-padding;
    line-height: $u-text-field-outline-label-line-height;
    transform: translate3d(0, -$u-text-field-outline-label-height / 2, 0);
  }

  .u-text-input {
    padding: $u-text-field-outline-label-height $u-text-field-outline-padding calc(#{$u-text-field-outline-padding-bottom} - #{$u-text-field-border-width});
    margin-top: -$u-text-field-outline-label-height;
    border-color: transparent !important;

    &:not(textarea) {
      height: $u-text-field-outline-height;
    }
  }

  &.u-text-field-no-label .u-text-input {
    margin-top: 0;
  }

  &.empty:not(.focus) {
    label {
      transform: translate3d(0, ($u-text-field-outline-height / 2) - ($u-text-field-outline-label-height / 2), 0) !important;
    }

    .u-text-field-leading-icon ~ label {
      transform: translate3d(calc(#{$u-text-field-icon-margin} + #{$u-text-field-icon-size}), ($u-text-field-outline-height / 2) - ($u-text-field-outline-label-height / 2), 0) !important;
    }
  }

  &::before {
    top: 0;
    height: auto;
    border: $u-text-field-border-width solid;
    border-radius: $u-text-field-outline-border-radius;
    transition: none;
    transform: none;
  }

  &.focus,
  &.invalid {
    &::before {
      border-width: $u-text-field-focus-border-width;
    }
  }

  ~ .u-text-field-helper-text {
    margin-right: $u-text-field-outline-padding;
    margin-left: $u-text-field-outline-padding;
  }

  .u-text-field-leading-icon,
  .u-text-field-trailing-icon {
    bottom: 50%;
  }

  .u-text-field-leading-icon {
    left: $u-text-field-icon-margin;

    ~ .u-text-input {
      margin-left: calc(#{$u-text-field-icon-margin} + #{$u-text-field-icon-size});
    }
  }

  .u-text-field-trailing-icon {
    right: $u-text-field-icon-margin;

    ~ .u-text-input,
    ~ label {
      margin-right: calc(#{$u-text-field-icon-margin} + #{$u-text-field-icon-size});
    }
  }
}

.u-search-field {
  border-radius: $u-search-field-border-radius;

  &::before {
    content: none;
  }

  .u-text-field-leading-icon,
  .u-text-field-trailing-icon {
    bottom: 50%;
  }

  .u-text-input {
    height: $u-search-field-height;
    padding: $u-search-field-elevated-padding;
    border: none;
  }

  .u-text-field-leading-icon {
    left: $u-text-field-icon-margin;

    ~ .u-text-input {
      margin-left: calc(#{$u-text-field-icon-margin} + #{$u-text-field-icon-size});
    }
  }

  .u-text-field-trailing-icon {
    right: $u-text-field-icon-margin;

    ~ .u-text-input,
    ~ label {
      margin-right: calc(#{$u-text-field-icon-margin} + #{$u-text-field-icon-size});
    }
  }
}

.u-search-field-elevated {
  box-shadow: $u-search-field-elevated-box-shadow;
}

%u-text-input-affix {
  flex-shrink: 0;
}

.u-text-input-suffix {
  @extend %u-text-input-affix;
  margin-left: $u-text-input-affix-margin;
}

.u-text-input-prefix {
  @extend %u-text-input-affix;
  margin-right: $u-text-input-affix-margin;
}

@mixin u-text-fields-theme {

  $theme-colors: map-get($u-theme, "colors");
  $dark-theme: map-get($u-theme, "dark");
  $uwp-style: map-get($u-theme, "uwp-style");

  $primary-color: map-get($theme-colors, "primary");
  $danger-color: map-get($theme-colors, "danger");

  $u-text-field-label-color: $u-text-field-label-color-light-theme;
  $u-text-field-divider-color: $u-text-field-divider-color-light-theme;
  $u-text-field-divider-color-hover: $u-text-field-divider-color-hover-light-theme;
  $u-text-field-divider-color-disabled: $u-text-field-divider-color-disabled-light-theme;
  $u-text-field-box-background: $u-text-input-box-bg-light-theme;
  $u-text-field-box-disabled-background: $u-text-input-box-disabled-bg-light-theme;
  $u-text-field-elevated-background: $u-search-field-elevated-bg-light-theme;
  $u-text-input-color: $u-text-input-color-light-theme;
  $u-text-input-placeholder-color: $u-text-input-placeholder-color-light-theme;
  $u-text-input-disabled-color: $u-text-input-disabled-color-light-theme;
  $u-text-input-icon-color: $u-text-input-icon-color-light-theme;
  $u-text-input-affix-color: $u-text-input-affix-color-light-theme;
  $u-text-field-helper-text-color: $u-text-field-helper-text-color-light-theme;
  $u-text-field-outline-label-background: map-get($u-theme, "bg-light");

  @if ($dark-theme) {
    $u-text-field-label-color: $u-text-field-label-color-dark-theme;
    $u-text-field-divider-color: $u-text-field-divider-color-dark-theme;
    $u-text-field-divider-color-hover: $u-text-field-divider-color-hover-dark-theme;
    $u-text-field-divider-color-disabled: $u-text-field-divider-color-disabled-dark-theme;
    $u-text-field-box-background: $u-text-input-box-bg-dark-theme;
    $u-text-field-box-disabled-background: $u-text-input-box-disabled-bg-dark-theme;
    $u-text-field-elevated-background: $u-search-field-elevated-bg-dark-theme;
    $u-text-input-color: $u-text-input-color-dark-theme;
    $u-text-input-placeholder-color: $u-text-input-placeholder-color-dark-theme;
    $u-text-input-disabled-color: $u-text-input-disabled-color-dark-theme;
    $u-text-input-icon-color: $u-text-input-icon-color-dark-theme;
    $u-text-input-affix-color: $u-text-input-affix-color-dark-theme;
    $u-text-field-helper-text-color: $u-text-field-helper-text-color-dark-theme;
    $u-text-field-outline-label-background: map-get($u-theme, "bg-dark");
  }

  .u-text-field-leading-icon,
  .u-text-field-trailing-icon {
    color: $u-text-input-icon-color;
  }

  .u-text-input,
  input.u-text-input {
    color: $u-text-input-color;
    border-color: $u-text-field-divider-color;
  }

  .u-text-field {
    label {
      color: $u-text-field-label-color;
    }

    &.focus {
      color: $primary-color;
    }

    &.disabled {
      label,
      .u-text-field-leading-icon,
      .u-text-field-trailing-icon,
      + .u-text-field-helper-text {
        &,
        .u-text-field-helper-text-default {
          color: $u-text-input-disabled-color;
        }
      }
    }

    &.disabled .u-text-input,
    .u-text-input:disabled {
      color: $u-text-input-disabled-color;
      border-color: $u-text-field-divider-color-disabled;
    }

    &.invalid,
    &.invalid .u-text-field-leading-icon,
    &.invalid .u-text-field-trailing-icon,
    & ~ .u-text-field-helper-text .u-text-field-helper-text-invalid {
      color: $danger-color;
    }

    &:not(.disabled):hover {
      .u-text-input:not(:disabled) {
        border-color: $u-text-field-divider-color-hover;
      }
    }
  }

  .u-text-field-box {
    background: $u-text-field-box-background;

    &.disabled {
      background: $u-text-field-box-disabled-background;
    }
  }

  .u-text-field-outline {
    label {
      background: $u-text-field-outline-label-background;
    }

    &:not(.focus):not(.invalid):not(.disabled) {
      &::before {
        border-color: $u-text-field-divider-color;
      }

      &:hover::before {
        border-color: $u-text-field-divider-color-hover;
      }
    }

    &.disabled::before {
      border-color: $u-text-field-divider-color-disabled;
    }
  }

  .u-search-field {
    background: $u-text-field-box-background;
  }

  .u-search-field-elevated {
    background: $u-text-field-elevated-background;
  }

  .u-text-field-helper-text {
    color: $u-text-field-helper-text-color;
  }

  .u-text-input::placeholder,
  .u-text-input-placeholder {
    &,
    .u-text-field-floating-label.empty.u-text-field-no-label &,
    .u-text-field-outline.empty.u-text-field-no-label &,
    .u-text-field-box.empty.u-text-field-no-label & {
      color: $u-text-input-placeholder-color;
    }
  }

  %u-text-input-affix {
    color: $u-text-input-affix-color;
  }
}
